.progressive{position : relative;display : block;overflow : hidden;outline : none;width : 100%;}
.progressive img{display : block;border : 0;outline : 0;width : 100%;height : auto;}
.progressive img.preview {-webkit-filter : blur(1vw);filter : blur(1vw);}
.progressive img.reveal {position: absolute;left: 0;top: 0;will-change: transform, opacity;-webkit-animation: progressiveReveal 1s ease-out;animation: progressiveReveal 1s ease-out;}
@-webkit-keyframes progressiveReveal {0% {opacity: 0;}100% {opacity: 1;}}
@keyframes progressiveReveal {0% {opacity: 0;}100% {opacity: 1;}}